*/
.testing-freight {
  font-family: "FreightTextBook", serif;
}

.container {
  margin-bottom: 1.875rem;
}

.container__inner {
  background-color: #ed3fa8;
  color: #fff;
  min-height: 18.75rem;
}

/**
 * docs
 */
.docs {
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 768px) {
  .docs {
    flex-direction: row;
  }
}

.docs-sidebar {
  border-bottom: 1px solid #eee;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .docs-sidebar {
    border-bottom: 0;
    border-right: 1px solid #eee;
    width: 9.375em;
  }
}

@media only screen and (min-width: 768px) {
  .docs-sidebar {
    width: 13.75em;
  }
}

.docs-nav {
  padding-top: 1.25em;
}

.docs-nav__list {
  display: flex;
  flex-wrap: wrap;
}

.docs-nav__item {
  margin-bottom: 0.625em;
}

@media only screen and (min-width: 768px) {
  .docs-nav__item {
    margin-bottom: 0;
    width: 100%;
  }
}

.docs-nav__item--group-name {
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .docs-nav__item--group-name {
    margin-bottom: 0.3125em;
  }
}

.docs-nav__link {
  display: block;
  padding: 0.3125em 0.9375em;
}

.docs-nav__item--group-name .docs-nav__link {
  font-weight: 700;
  padding: 0.3125em 0 0;
}

.docs-content {
  padding-top: 1.25em;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .docs-content {
    padding-left: 1.875em;
    padding-top: 0;
    width: calc(100% - 9.375em);
  }
}

@media only screen and (min-width: 1025px) {
  .docs-content {
    padding-left: 3.125em;
    width: calc(100% - 13.75em);
  }
}

.docs-group__hdg {
  font-size: 2em;
}

.docs-group__content {
  padding: 1.25em 0;
}

.docs-sec {
  margin-bottom: 3.125em;
}

.docs-sec__hdg {
  background-color: #eee;
  border-bottom: 1px solid #ddd;
  margin-bottom: 1.25rem;
  padding: 0.625rem 0.9375rem;
}

.docs-fieldset {
  margin-bottom: 1.875em;
}

